@font-face {
  font-display: swap;
  font-family: 'Droid';
  src: local("Droid"),
       local("Droid Regular"),
       local("Droid-Regular"),
       url("../fonts/Droid/regular/Droid-Regular.woff2") format("woff2"),
       url("../fonts/Droid/regular/Droid-Regular.woff") format("woff"),
       url("../fonts/Droid/regular/Droid-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

