@font-face {
  font-family: 'Dangrek';
  src: url('Dangrek.eot');
  src: local('Dangrek'),
      /*url('Dangrek.eot?#iefix') format('embedded-opentype'),*/
      url('../fonts/Dangrek/regular/Dangrek.woff2') format('woff2'),
      url('../fonts/Dangrek/regular/Dangrek.woff') format('woff'),
      url('../fonts/Dangrek/regular/Dangrek.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-display: swap;
  font-family: 'DINAlternate';
  src: local("DINAlternate"),
       local("DIN-Alternate"),
       local("DINAlternate-Regular"),
       local("DIN-Alternate-Regular"),
       url("../fonts/DIN-Alternate/bold/DINAlternate-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}

